body {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

.main {
  width: 100%;
  height: 100vh;
  color: #1e1e1e;
  font-family: Inter;
  font-size: 14px;
  display: flex;
}

.nav {
  width: 30%;
  height: 100%;
  background-color: #fbfbfb;
  border-right: 1px solid #d2d2d2;
  flex-direction: column;
  flex-shrink: .5;
  gap: .5rem;
  padding: 1rem;
  display: flex;
}

.nav__item {
  color: #999;
  font-weight: 500;
}

.nav__item:hover {
  color: #3369f3;
}

.content {
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.content__title {
  color: #999;
}

.error-page {
  height: 100vh;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  display: flex;
}

.error-page__info {
  font-family: Inter;
  font-size: 20px;
}

.back-slide {
  z-index: 1;
  width: 3rem;
  height: 100vh;
  background-color: #fbfbfb;
  border: 1px solid #d2d2d2;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.back-arrow {
  height: 1.5rem;
  width: 1.5rem;
  color: #fff;
  background-color: #3369f3;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  display: flex;
}

.back-arrow:hover {
  opacity: .7;
}

.link {
  color: #3369f3;
  cursor: pointer;
  font-family: Inter;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
}

.button {
  cursor: pointer;
  color: #fff;
  width: 280px;
  height: 37px;
  background: #3369f3;
  border: none;
  border-radius: 8px;
  outline: none;
  font-weight: 500;
}

.button:hover {
  opacity: .8;
}

.button__ghost {
  color: #3369f3;
  background: #fff;
}

.button__ghost:hover {
  opacity: .6;
  background: #1976d20a;
}

