.inputItem {
  padding: 15px 0 0;
  position: relative;
}

.inputItem__input {
  width: 100%;
  background: none;
  border: none;
  border-bottom: 1px solid #d2d2d2;
  outline: 0;
  padding: 7px 0;
  font-size: 1rem;
  transition: border-color .2s;
}

.inputItem__input::placeholder {
  color: #0000;
}

.inputItem__input:placeholder-shown ~ .inputItem__label {
  cursor: text;
  font-size: 1rem;
  top: 20px;
}

.inputItem__input:focus {
  border-bottom: 2px solid #3369f3;
  padding-bottom: 6px;
}

.inputItem label, .inputItem__input:focus ~ .inputItem__label {
  color: #3369f3;
  font-size: .8rem;
  transition: all .2s;
  display: block;
  position: absolute;
  top: 0;
}

.inputItem label {
  color: #999;
}

.inputItem-error:after {
  content: "Data is not correct";
  color: red;
  position: absolute;
  bottom: -25px;
  right: 0;
}

.inputItem-error__input {
  border-bottom: 1px solid red;
}

.inputItem-error__input:focus {
  border-bottom: 2px solid red;
}

.inputItem-error label, .inputItem-error__input:focus ~ .inputItem-error__label {
  color: red;
}

